import React, { Component } from "react";
import './add.css'



export default class AddTodo extends Component {
    state = {
        Id: "",
        Title: "",
        Status: "Pending"
    };
handleIdChange = (event) => {
        this.setState({
            Id: event.target.value
        });
    };
    handleTitleChange = (event) => {
        this.setState({
            Title: event.target.value
        });
    }
    handleStatusChange = (event) => {
        this.setState({
            Status: event.target.value
        });
    };
    handleToDoSubmit = (event) => {
        event.preventDefault();
        this.props.onAdd({
            Id: this.state.Id,
            Title: this.state.Title,
            Status: this.state.Status
        });
        this.setState({
            Id: "",
            Title: "",
            Status: "Pending"
        });
    };
    render() {
        return (
  
            <div className="body">
                <div className="card-form">
                    <form onSubmit={this.handleToDoSubmit} >
                    <div className="form-title">Add ToDo</div>
                    <div className="form-body">
                        <div className="row">
                        <input value={this.state.Id} 
                            onChange={this.handleIdChange}
                            type="text"
                            placeholder="Enter Id" />
                        <input value={this.state.Title} 
                            onChange={this.handleTitleChange}
                            type="text" 
                            placeholder="Enter Title" />
                        </div>
                        <div className="row">
                        <select className="select" value={this.state.Status} onChange={this.handleStatusChange} >
                            <option value="Done" >Done</option>
                            <option value="Pending" >Pending</option>
                        </select>
                        </div>
                        <div class="form-footer">
                            <button type="submit"  className="form-control btn btn-success" >Add Todo</button>
                        </div>
                    </div>
                    </form>
                </div>
            </div>
        );
    }
}


